.mobilenav {
	list-style: none;
	display: flex;
	justify-content: space-around;
	align-items: center;
	width: 100%;
	position: fixed;
	bottom: 0;
	bottom: env(safe-area-inset-bottom);
	background-color: #eaddff;
	margin: 0;
	padding: 16px 0 16px;
}

.mobilenav__item {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	width: 6rem;
}

.mobilenav__link {
	text-decoration: none;
	border-radius: 30px;
	transition: all 0.3s;
	margin: 0;
	display: flex;
	flex-direction: column;
	align-items: center;
	color: inherit;
	width: 100%;
}

.mobilenav__label {
	margin: 0;
	font-weight: 500;
}

.material-icons {
	transition: background-color 0.3s;
	transition: padding 0.3s;
	font-weight: 500;
	background-color: #d0bcff;
	padding: 5px 22px;
	border-radius: 30px;
	margin: 0 0 5px;
}

.mobilenav__link:not(.active) .material-icons {
	background-color: transparent;
	transition: all 0.3s;
	padding: 5px 0;
}

.mobilenav__link:not(.active) .mobilenav__label {
	font-weight: 400;
}