.header {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 2rem 20% 2rem;
	margin: 0 auto 2rem;
	font-weight: 600;
	font-size: 3vh;
	background-color: #F6EDFF;
	box-shadow: inset 0 -3px 10px -5px white, 0 -2px 8px #49454F;
}

.header__avatar {
	width: 3rem;
	height: 3rem;
	background-color: #EFB8C8;
	color: #31111D;
	border-radius: 50%;
	text-align: center;
	font-weight: 500;
	font-size: 2rem;
	display: flex;
	justify-content: center;
	align-items: center;
	line-height: 2rem;
}

.header__user-pic {
	width: 3rem;
	height: 3rem;
	border-radius: 50%;
	margin-left: 50px;
}

@media screen and (max-width: 424px) {
	.header {
		padding: 1rem 10% 1rem;
		margin: 0 auto 2rem;
		font-size: 3vh;
	}

	.header__avatar {
		width: 2.5rem;
		height: 2.5rem;
		font-weight: 500;
		font-size: 1.5rem;
		line-height: 1.5rem;
	}
	
	.header__user-pic {
		width: 3rem;
		height: 3rem;
		border-radius: 50%;
		margin-left: 50px;
	}
}